	module.exports = {"data":{"markdownRemark":{"html":"<h2>Intent</h2>\n<p>Being able to conduct TDD is one of the key skills developers need these days. It encourages reusable, simple and testable code. This step will introduce TDD into their skillset and prepare them for future steps.</p>\n<p>Rather than reinvent the wheel This step is just to complete one or more katas from codingdojo.org. This is fairly quick step and TDD will be applied in the next step. </p>\n<p>After the step is completed: </p>\n<ul>\n<li>Ask the pupil if they had any difficulties/why</li>\n<li>Get the pupil to take you through their code and ask them to show it working. </li>\n</ul>\n<h2>Program</h2>\n<p>Fairly good write up on Wikipedia.</p>\n<p><a href=\"https://en.wikipedia.org/wiki/Test-driven_development\">https://en.wikipedia.org/wiki/Test-driven_development</a></p>\n<p>Read up on TDD and complete the bowling Kata with a TDD approach. It is recommended to TDD multiple katas, although the requirement is just the once. </p>\n<p><a href=\"http://codingdojo.org/kata/Bowling/\">http://codingdojo.org/kata/Bowling/</a></p>\n<p><a href=\"../shopping2\">&#x3C; Prev</a> | <a href=\"../shopping3\">Next ></a></p>","frontmatter":{"path":"/TDD","title":"Test Driven Development"}}},"pathContext":{}}

